import http from '@/request/request.js';
import { PUBLICDATA,GET_STORAGE,APIBASE } from '@/common/globalConfig.js'

// 
export function walletCash(data) {
	return http.post(`/center/walletCash`, {
		data:data,
	    header:{
		  'user-token':GET_STORAGE('token')
	    }
	});
};
